<template>
  <div class="container">
    <div class="list-content" v-if="list.length">
        <div class="container">
            <h2 class="list-content__title">{{ title }}</h2>
        </div>
      <router-link
        :to="`/${main}/${item.id}`"
        class="list-content__item"
        v-for="(item, index) in list"
        :key="index"
      >
        <div class="list-content__item-scelet"></div>
        <img
          :src="
            'https://www.themoviedb.org/t/p/w300_and_h450_bestv2' +
            item.poster_path
          "
          loading="lazy"
          alt=""
        />
      </router-link>
      <div class="container center item" v-if="loading">
        <div class="spinner"></div>
      </div>
    </div>
    <div class="list-content" v-else>
      <div class="container center">
        <div class="spinner"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    main: {
      type: String,
      required: true,
    },
    title: {
        type: String,
        required: true
    }
  },
  data() {
    return {
      page: 1,
      loading: false,
      list: [],
    };
  },
  mounted() {
    window.addEventListener("scroll", () => {
      if (window.scrollY + window.innerHeight >= document.body.scrollHeight) {
        this.loadMore();
      }
    });
    this.loadMore();
  },
  methods: {
    async loadMore() {
      if (!this.loading) {
        this.loading = true;
        // await new Promise((res) => setTimeout(() => res("foo"), 700));
        await this.$store.dispatch(`${this.main}page/getMovies`, this.page);
        const listAwait = this.$store.getters[`${this.main}page/movies`];
        const list = [...listAwait];
        this.list.push(...list);
        console.log(this.page, this.list.length);
        this.loading = false;
        this.page++;
      }
    },
  },
};
</script>